.portfolio{

    .portfolio__container{
        margin: 0 auto;

        .services__content{
            position: relative;
            z-index: 10;

            .tab_container{
                margin-top: vw(40);
            }

            .portfolio__header{
                display: grid;
                align-items: center;
                grid-template-columns: vw(600) 1fr;
                position: relative;
                z-index: 15;
            }
        }
    }

}

#portfolio{
    width: vw(600);
    margin: 0;

    &::after{
        width: vw(851);
        left: vw(-246);
        transform: translateX(0);
    }

    li{
        &.active{

            &::after{
                width: vw(97);
            }

            &:nth-child(2)::after,
            &:nth-child(3)::after{
                left: 50%;
                transform: translate(-50%);
            }

            &:first-child:after{
                left: 50%;
                transform: translate(-50%);
            }

            &:last-child:after{
                right: vw(-5);
            }
        }

        a{
            span.mob{
                display: none;
            }
        }
    }
}

.portfolio__title{
    width: 100%;
    margin-left: vw(60);

    p.portfolio__header{
        font-family: $DTW-Bold;
        font-size: vw(36);
        text-transform: uppercase;
        background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.79) 0%, rgba(255, 255, 255, 0.26) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }

    p.portfolio__text{
        color: white;
        font-size: vw(44);
        font-family: $DTW-Bold;
        margin-top: vw(40);
    }
}

.portfolio__images{
    position: absolute;
    width: vw(1039);
    height: vw(1065);
    left: vw(-250);
    z-index: 5;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

//============================

.portfolio__slider{
    position: relative;
    height: vw(610);
    z-index: 25;
}

.slider__container{
    height: vw(610);
}

.slider__elements{
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 0;
    .slider__item{
        width: vw(403);
        height: vw(455);
        background-color: #1AC7FD;
        border-radius: vw(14);
        box-shadow: 0px vw(23) vw(52) rgba(0, 0, 0, 0.21);
        position: relative;
        margin-right: vw(40);

        &::before{
            content: '';
            position: absolute;
            width: vw(3);
            height: calc(100% - vw(13));
            background-color: rgba(#fff, 0.24);
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            border-radius: vw(14) 0 0 vw(14);
        }


        .slider__item__background{
            width: vw(403);
            height: vw(455);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: vw(14);
            }
        }
    }

    .slider__item--active{
        height: vw(600);
        border-radius: vw(14);

        .slider__item__background{
            height: vw(600);

            img{
                border-radius: vw(14);
            }
        }
    }
}

.slider__buttons{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: vw(35);
    left: vw(443);

    .btn__arrow__stroke{
        width: vw(74);
        height: vw(74);
        position: relative;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            opacity: .2;
        }
    }

    .slider__arrow--next.active,
    .slider__arrow--prev.active{

        .btn__arrow__stroke{

            img{
                opacity: 1;
            }
        }
    }

    .btn__arrow{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: vw(60);
        height: vw(60);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        .btn__img{
            width: vw(8);
            height: vw(14);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }

        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 50%;
            padding: vw(8);
            background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);;
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
        }
    }

    .slider__arrow--prev{
        margin-right: vw(30);

        img{
            transform: rotate(-180deg);
        }

        .btn__arrow{

            .btn__img{

                img{
                    transform: rotate(0);
                }
            }
        }
    }

}

//============================

@media (max-width: 990px){
    .portfolio__images{
        display: none;
    }

    .portfolio{

        .portfolio__container{
    
            .services__content{

                .tab_container{
                    margin-top: vmin(25);
                }

                .portfolio__header{
                    display: flex;
                    flex-direction: column;
                }
            }
        }
    }

    .portfolio__title{
        order: 1;
        margin-left: 0;

        p.portfolio__header{
            font-size: vmin(24);
            text-align: center;
        }

        p.portfolio__text{
            font-size: vmin(16);
            text-align: center;
            margin-top: vmin(20);
        }
    }

    #portfolio{
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: vmin(25);
        width: 100%;
        order: 2;

        li{
            a{
                span.mob{
                    display: block;
                }

                span.desktop{
                    display: none;
                }
            }
        }
    }
}